.series-container {
  width: 90%;
  margin: auto;
}

.fade-enter {
  opacity: 0;
  transform: scaleY(-10rem);
}

.fade-enter-active {
  opacity: 1;
  transition: all 0.7s;
  transform: scaleY(0);
}

.fade-exit {
  opacity: 1;
}

.fade-exit-active {
  opacity: 1;
  transition: all 0.1s;
}
